.login-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin: 20px 0px;
}
.login-main{
    display: flex;
    flex-direction: column;
    align-items: center;
     border: 1px solid 	#D3D3D3;
      width: 350px;
      height: 300px;
}

.login-input{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 10px 0px;
    width: 250px;
    height: 40px;
    border: 1px solid 	#D3D3D3;
    border-radius: 3px;
    font-family: 'Lato', 'Open Sans', sans-serif;
}
.login-input::placeholder{
    color: grey;
}
.login-btn{
    width: 250px;
    height: 40px;
    background-color: rgb(17, 93, 192);
    border: 1px solid  rgb(17, 93, 192);
    border-radius: 3px;
    color: white;
    font-family: 'Lato', 'Open Sans', sans-serif;
    cursor: pointer;
}

.forget-login-btn{
    margin: 10px 0px;
    border: none;
    cursor: pointer;
    color: black;
    font-family: 'Lato', 'Open Sans', sans-serif;
}

.login-link{
    margin: 20px, 0px;
    text-decoration: none;
    color: grey;
    font-family: 'Lato', 'Open Sans', sans-serif;

}


@media screen ,(max-width: 1038px) {
    .login-container{
        min-width: 970px;
    }
   
  }